	module.exports = {"data":{"markdownRemark":{"html":"<p>外でやたら和太鼓の音が聞こえるなぁ、と思ったら家の裏手の神社で夏祭りやってたので、こっそり参加してきましたよ？ 以前の日記にも書いたようにこの神社、オヤシｒ…ｹﾞﾌﾝｹﾞﾌﾝじゃなくて鬼を祀っている世にも珍しい神社で、拝殿の裏手には大量の金棒が奉納されている物々しい雰囲気の神社なのですが、この日だけはそんな雰囲気もどこへやら。ここの鬼さんもきっと酒と宴会だけは大好きなのでしょう。  自分も酒と西瓜を片手に鬼さんの宴を一刻程楽しんできましたが今日は折角の休みの日、ここ一週間ほど帰っても寝るだけと言ってもいいくらい暇のない毎日だったので、今のうちに夢終劇の制作を進めとかなきゃ、ということでPCと共に自室に引きこもってきます。 (時間を)持ってかないでー</p>","frontmatter":{"cover":null,"date":"2006-08-26T21:01:00+09:00","strDate":"2006/8/26","title":"鬼が住まう社の宴"}}},"pathContext":{"path":"/ja/blog/2006/08/26-鬼が住まう社の宴/","langKey":"ja"}}

